/* font face define for site */
@font-face {
  font-family: 'VodafoneRg';
  src: url('../fonts/VodafoneRg.eot');
  src: url('../fonts/VodafoneRg.eot') format('embedded-opentype'),  
    url('../fonts/VodafoneRg.woff2') format('woff2'),  
    url('../fonts/VodafoneRg.woff') format('woff'),  url('../fonts/VodafoneRg.ttf') format('truetype'),  
    url('../fonts/VodafoneRg.svg#VodafoneRg') format('svg');
}
