/**** break points ****/
/* base for idg */
html {
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  font-family: "VodafoneRg", Helvetica, sans-serif;
  font-size: 19px;
  line-height: 1.42857143;
  color: #000;
  background-color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
}

h1 {
  font-size: "";
}

h2 {
  font-size: "";
}

h3 {
  font-size: "";
}

h4 {
  font-size: "";
}

h5 {
  font-size: 19px;
}

h6 {
  font-size: "";
}

p {
  margin: 0 0 13.5px;
}

a {
  color: inherit;
  text-decoration: none;
}

/********* for order list ***********/
ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

/********* blockquote *********/
blockquote {
  margin: 0 0 1rem;
}

/********* strong ************/
b,
strong {
  font-weight: bolder;
}

/********* pre **************/
pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
}

/*********** Figures **********/
figure {
  margin: 0 0 1rem;
}

/********** Images and content *********/
img {
  max-width: inherit;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

/******** touch ***************/
a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

/*********** table *************/
table {
  border-collapse: collapse;
}

/************ label *************/
label {
  display: inline-block;
  margin-bottom: .5rem;
}

/********** form browsers ref *********/
input,
button,
select,
optgroup,
textarea {
  margin: 0;
}

button,
select {
  text-transform: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

