/*
** style for google search.
*/
.block-search {
  text-align: right;
  padding-right: 30px;
}

@media (max-width: 768px) {
  .block-search {
    padding-left: 7px;
    margin-bottom: 8px;
    padding-top: 8px;
    text-align: left;
    max-width: 100%;
    flex: 0 0 100%;
  }
}

.block-search .form-search {
  position: relative;
}

.block-search .google_search_text {
  border: 1px solid #fff;
  height: 32px;
  padding: 0 0 0 5px;
  width: 318px;
}

@media (max-width: 768px) {
  .block-search .google_search_text {
    width: 88%;
  }
}

.block-search .google_search_text input {
  outline: none;
}

.block-search .search {
  background: url("../images/search.png") no-repeat scroll 0px 4px #fff;
  border: 1px solid #fff;
  cursor: pointer;
  height: 32px;
  margin: 0 0 0 -5px;
  text-indent: -9999px;
  width: 30px;
}

.block-search .search:hover, .block-search .search:focus {
  background: url("../images/search.png") no-repeat scroll 1px 5px #fff;
  border: none;
}

